<template><section><html><head></head><body><h1>EllipsoidGraphics</h1>
<p><code v-pre="">vc-graphics-ellipsoid</code> 组件用于加载 (椭)球体。需要作为 <code v-pre="">vc-entity</code> 的子组件使用。</p>
<h2>示例</h2>
<h3>加载(椭)球体</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-entity ref="entity1" :position="position1" :description="description" :ellipsoid.sync="ellipsoid1">
          <vc-graphics-ellipsoid :radii="radii1" :material="material1" :outline="true"></vc-graphics-ellipsoid>
        </vc-entity>
        <vc-entity ref="entity2" :position="position2" :description="description" :ellipsoid.sync="ellipsoid2">
          <vc-graphics-ellipsoid :radii="radii2" :outline="true" :material="material2" :outline-color="outlineColor2"></vc-graphics-ellipsoid>
        </vc-entity>
        <vc-entity ref="entity3" :position="position3" :description="description" :ellipsoid.sync="ellipsoid3">
          <vc-graphics-ellipsoid :radii="radii3" :fill="false" :outline="true" :outline-color="outlineColor3" :slice-partitions="24" :stack-partitions="36"></vc-graphics-ellipsoid>
        </vc-entity>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
