        <span class="hljs-attr">material1</span>: <span class="hljs-string">'BLUE'</span>,

        <span class="hljs-attr">ellipsoid2</span>: {},
        <span class="hljs-attr">position2</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">107.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">40.0</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">300000.0</span> },
        <span class="hljs-attr">radii2</span>: { <span class="hljs-attr">x</span>: <span class="hljs-number">300000.0</span>, <span class="hljs-attr">y</span>: <span class="hljs-number">300000.0</span>, <span class="hljs-attr">z</span>: <span class="hljs-number">300000.0</span> },
        <span class="hljs-attr">outlineColor2</span>: <span class="hljs-string">'BLACK'</span>,
        <span class="hljs-attr">material2</span>: {},

        <span class="hljs-attr">ellipsoid3</span>: {},
        <span class="hljs-attr">position3</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">100.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">40.0</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">300000.0</span> },
        <span class="hljs-attr">radii3</span>: { <span class="hljs-attr">x</span>: <span class="hljs-number">200000.0</span>, <span class="hljs-attr">y</span>: <span class="hljs-number">200000.0</span>, <span class="hljs-attr">z</span>: <span class="hljs-number">300000.0</span> },
        <span class="hljs-attr">outlineColor3</span>: <span class="hljs-string">'YELLOW'</span>
      }
    },
    <span class="hljs-title function_">mounted</span>(<span class="hljs-params"></span>) {
      <span class="hljs-title class_">Promise</span>.<span class="hljs-title function_">all</span>([<span class="hljs-variable language_">this</span>.<span class="hljs-property">$refs</span>.<span class="hljs-property">entity1</span>.<span class="hljs-property">createPromise</span>, <span class="hljs-variable language_">this</span>.<span class="hljs-property">$refs</span>.<span class="hljs-property">entity2</span>.<span class="hljs-property">createPromise</span>, <span class="hljs-variable language_">this</span>.<span class="hljs-property">$refs</span>.<span class="hljs-property">entity3</span>.<span class="hljs-property">createPromise</span>]).<span class="hljs-title function_">then</span>(
        <span class="hljs-function">(<span class="hljs-params">instances</span>) =&gt;</span> {
          instances[<span class="hljs-number">0</span>].<span class="hljs-property">viewer</span>.<span class="hljs-title function_">zoomTo</span>(instances[<span class="hljs-number">0</span>].<span class="hljs-property">viewer</span>.<span class="hljs-property">entities</span>)
