<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 指定 ellipsoid 是否显示。</td>
</tr>
<tr>
<td>radii</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 ellipsoid 的半径参数。 <strong>结构： { x: number, y: number, z: number }</strong></td>
</tr>
<tr>
<td>heightReference</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> 指定 ellipsoid 高度模式。 <strong>NONE: 0, CLAMP_TO_GROUND: 1, RELATIVE_TO_GROUND: 2</strong></td>
</tr>
<tr>
<td>fill</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 指定 ellipsoid 是否填充材质。</td>
</tr>
<tr>
<td>material</td>
<td>Object|String|Array</td>
<td><code v-pre="">'WHITE'</code></td>
<td><code v-pre="">optional</code> 指定 ellipsoid 材质。</td>
</tr>
<tr>
<td>outline</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code> 指定 ellipsoid 是否绘制轮廓线。</td>
</tr>
<tr>
<td>outlineColor</td>
<td>Object|String|Array</td>
<td><code v-pre="">'BLACK'</code></td>
<td><code v-pre="">optional</code> 指定 ellipsoid 轮廓线颜色。</td>
</tr>
<tr>
<td>outlineWidth</td>
<td>Number</td>
<td><code v-pre="">1.0</code></td>
<td><code v-pre="">optional</code> 指定 ellipsoid 轮廓线宽度。</td>
</tr>
<tr>
<td>stackPartitions</td>
<td>Number</td>
<td><code v-pre="">64</code></td>
<td><code v-pre="">optional</code> 指定 ellipsoid 横向线数量。</td>
</tr>
<tr>
<td>slicePartitions</td>
<td>Number</td>
<td><code v-pre="">64</code></td>
<td><code v-pre="">optional</code> 指定 ellipsoid 径向线数量。</td>
</tr>
<tr>
<td>subdivisions</td>
<td>Number</td>
<td><code v-pre="">128</code></td>
<td><code v-pre="">optional</code> 指定 ellipsoid 每个轮环的样本数，确定曲率粒度。</td>
</tr>
<tr>
<td>shadows</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> 指定 ellipsoid 是否投射或接受每一个光源的阴影。 <strong>DISABLED: 0, ENABLED: 1, CAST_ONLY: 2, RECEIVE_ONLY: 3</strong></td>
</tr>
<tr>
<td>distanceDisplayCondition</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 ellipsoid 随相机距离的显示条件。 <strong>结构：{ near: number, far: number }</strong></td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考官方文档： <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/EllipsoidGraphics.html">EllipsoidGraphics</a></strong></li>
</ul>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>definitionChanged</td>
<td></td>
<td>每当更改或修改属性或子属性时触发该事件。</td>
</tr>
</tbody>
</table>
<hr>
</body></html></section></template>

<script>
    export default {
      data() {
        return {
          description: 'Hello Vue Cesium',
          ellipsoid1: {},
          position1: { lng: 114.0, lat: 40.0, height: 300000.0 },
          radii1: { x: 200000.0, y: 200000.0, z: 300000.0 },
          material1: 'BLUE',

          ellipsoid2: {},
          position2: { lng: 107.0, lat: 40.0, height: 300000.0 },
          radii2: { x: 300000.0, y: 300000.0, z: 300000.0 },
          outlineColor2: 'BLACK',
          material2: {},

          ellipsoid3: {},
          position3: { lng: 100.0, lat: 40.0, height: 300000.0 },
          radii3: { x: 200000.0, y: 200000.0, z: 300000.0 },
          outlineColor3: 'YELLOW'
        }
      },
      mounted() {
        Promise.all([this.$refs.entity1.createPromise, this.$refs.entity2.createPromise, this.$refs.entity3.createPromise]).then(
          (instances) => {
            instances[0].viewer.zoomTo(instances[0].viewer.entities)
          }
        )
      },
      methods: {
        ready(cesiumInstance) {
          const { Cesium, viewer } = cesiumInstance
          this.material2 = Cesium.Color.RED.withAlpha(0.5)
        }
      }
    }
  </script>